import React from "react";
import styled from "styled-components";
import { useAddStudent } from "../../../Context/AddStudent";
const OpenExcelFile = React.lazy(() => import("./OpenExcelFile"));

const ImportDocument = () => {
  const students = useAddStudent();
  return (
    <Panel>
      <LeftPanel />
      <ImportFile>
        {students.rows.length > 0 && <OpenExcelFile />}
        {students.rows.length < 1 && (
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              marginBottom: "10px",
              width: "100%",
            }}
          >
            <Title style={{ marginTop: "1.3rem" }}>Choose a XLSX File</Title>
            <Image
              style={{
                width: "150px",
                margin: "1.5rem auto",
                background: "transparent",
              }}
              src={require("../../images/xlsx.png")}
            />
            <Input
              name="oFile"
              type="file"
              accept="xlsx"
              onChange={(e) => {
                const file = e.target.files[0];
                students.setXlsxFile(file);
              }}
            />
            <Link onClick={() => students.handleDownload()}>
              Download Sample File
            </Link>
          </div>
        )}

        <ButtonPanel>
          <MyButton
            onClick={() => {
              students.handleIsImport(false);
            }}
          >
            Back
          </MyButton>
          {students.array.length > 0 && (
            <MyButton onClick={students.handleImportSubmit}>Upload</MyButton>
          )}
        </ButtonPanel>
      </ImportFile>
    </Panel>
  );
};

export default ImportDocument;

const Panel = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  width: 760px;
  border-radius: 5px;
  border: 1px solid rgba(3, 3, 3, 0.3);
  background: rgba(240, 240, 240, 0.5);
  box-shadow: 1px 1px 10px 10px rgba(3, 3, 3, 0.15);

  @media (max-width: 768px) {
    width: 98%;
  }
`;

const FlexColumn = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
`;

const ImagePanel = styled.div`
  width: ${(props) => (props.extend ? "75%" : "10%")};
  height: 100%;
  background-color: rgba(19, 15, 64, 1);
  min-height: 550px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  transition: all 0.5s ease;

  @media (max-width: 768px) {
    display: none;
  }
`;

const Text = styled.p`
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  text-align: center;
  width: 70%;
`;

const HeaderTitle = styled.p`
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  text-align: center;
  color: white;
  @media (max-width: 768px) {
    font-size: 18px;
  }
  @media (max-width: 480px) {
    font-size: 16px;
  }
`;

const Image = styled.img`
  width: 90px;
  height: auto;
  border-radius: 10px;
  margin-bottom: 15px;
  background-color: #fff;
`;

const ImportFile = styled.div`
  padding: 1rem;
  width: 100%;
  height: 100%;
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 610px) {
    width: 95%;
    padding: 0.5rem;
  }
`;

const Input = styled.input`
  padding: 0.3rem 1rem 0.3rem 0.3rem;
  border: 1px solid rgba(3, 3, 3, 0.5);
  border-radius: 5px;
  margin-top: 1.5rem;
  width: 100%;
`;

const MyButton = styled.button`
  font-weight: 500;
  background: rgba(230, 230, 230, 0.4);
  margin: 0.5rem 0 0.5rem 0.5rem;
  min-width: 100px;

  &:nth-child(1) {
    background: rgba(255, 71, 87, 1);
    color: #fff;
    border-radius: 3px;
  }
  &:nth-child(2) {
    background: rgba(55, 66, 250, 1);
    color: #fff;
    border-radius: 3px;
  }
  &:hover {
    &:nth-child(1) {
      background: rgba(255, 100, 87, 1);
    }
    &:nth-child(2) {
      background: rgba(55, 100, 250, 1);
    }
  }
`;

const ButtonPanel = styled.div`
  display: flex;
  align-items: end;
  justify-content: flex-end;
  margin-top: 0.1rem;
  width: 100%;
`;

const Title = styled.h1`
  font-size: 1.3rem;
  font-weight: 700;
  text-align: center;
`;

const Link = styled.p`
  font-size: 0.9rem;
  font-weight: 400;
  text-align: right;
  cursor: pointer;
  margin: 0.5rem 0 0.5rem auto;
  display: table-cell;

  &:hover {
    color: blue;
    text-decoration: underline;
  }

  @media (max-width: 768px) {
    font-size: 0.9rem;
  }
`;

function LeftPanel() {
  const students = useAddStudent();
  return (
    <ImagePanel extend={!students.rows.length > 0}>
      <FlexColumn
        style={
          !students.rows.length > 0 ? { display: "flex" } : { display: "none" }
        }
      >
        <FlexColumn>
          <Image src={require("../../images/xlsx-reader-excel-viewer-1.png")} />
          <HeaderTitle>Import a XLSX File</HeaderTitle>
          <Text>
            Please Select a .xlsx file to import. Because only .xlsx file
            support.
          </Text>
        </FlexColumn>
        <Text
          style={{
            textAlign: "left",
            fontSize: "0.8rem",
            marginBottom: "0",
            width: "80%",
            margin: "auto 0 0 0",
          }}
        >
          1st column should be Student ID
        </Text>
        <Text
          style={{
            textAlign: "left",
            fontSize: "0.8rem",
            width: "80%",
          }}
        >
          2nd column should be Student Name.
        </Text>
      </FlexColumn>
    </ImagePanel>
  );
}
